import React, { Component } from 'react';
import { IonContent, IonList, IonPage, IonInput, IonButton, IonItem, IonLabel, IonRow, IonCol, IonToggle, IonImg, IonIcon } from '@ionic/react';
import PropTypes from 'prop-types'
import './Login.page.scss';
import { Plugins, MotionEventResult } from '@capacitor/core';

const { Motion, Geolocation } = Plugins;

const props = {
    userData: PropTypes.any,
    loading: PropTypes.bool,
    loginError: PropTypes.bool,
    handleSubmit: PropTypes.func.isRequired
}

type propTypes = PropTypes.InferProps<typeof props>;

class LoginPage extends Component<propTypes, any> {

    constructor(props: any) {

        super(props)

        this.state = {

            X: 0,

            Y: 0,

            Z: 0,

            u: 0,

            gps: {},
            motion: {}
        }

    }

    componentDidMount() {
        //const { X, Y, Z } = this.state;

        Motion.addListener('accel', async (result: MotionEventResult) => {
            this.setState({ motion: { first: Math.round(Math.abs(result.acceleration.x * 2.236936)), second: Math.round(Math.abs(((result.acceleration.x) * 2.236936 / 250) * 180)) } });
        })


        var watch = Geolocation.watchPosition(
            { enableHighAccuracy: false, maximumAge: Infinity, timeout: 5000 },
            (result: any, error: any) => {
                if (result) {
                    this.setState({ gps: { first: Math.round(Math.abs(result.coords.speed * 2.236936)), second: Math.round(Math.abs(((result.coords.speed) * 2.236936 / 250) * 180)) } });
                } else {
                    console.log('GPS Error', error);
                }
            }
        );
    }

    render() {
        return (
            <div style={{margin:"10px"}}>
                <h1>IONIC Capacitor Accelerometer </h1>
                <div>-------------------------------------------------</div>
                <h1>first motion calculation for speed : </h1>
                <div>{this.state.motion.first} MPH</div>
                <br />
                <h1>second motion calculation for speed : </h1>
                <div>{this.state.motion.second} MPH </div>
                <br />
                <br />
                <h1>IONIC Capacitor gpslocation </h1>
                <div>-------------------------------------------------</div>
                <h1>first gps calculation for speed : </h1>
                <div>{this.state.gps.first} MPH </div>
                <br />
                <h1>second gps calculation for speed : </h1>
                <div>{this.state.gps.second} MPH </div>
            </div>

        )

    }
}

export default LoginPage;